'use client'

import React from "react"

import { useEffect, useState } from 'react'
import { getNotifications, markNotificationAsRead } from '@/lib/api-service'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Skeleton } from '@/components/ui/skeleton'
import { Separator } from '@/components/ui/separator'
import {
  AlertCircle,
  CheckCircle2,
  MessageSquare,
  Calendar,
  Zap,
  Clock,
  X,
} from 'lucide-react'
import { toast } from 'sonner'
import { cn } from '@/lib/utils'

export interface Notification {
  id: string
  type: 'alert' | 'success' | 'message' | 'task' | 'deal' | 'lead'
  title: string
  description: string
  read: boolean
  createdAt: string
  link?: string
}

interface NotificationsListProps {
  compact?: boolean
}

const NOTIFICATION_ICONS: Record<string, React.ReactNode> = {
  alert: <AlertCircle className="h-4 w-4 text-destructive" />,
  success: <CheckCircle2 className="h-4 w-4 text-green-600" />,
  message: <MessageSquare className="h-4 w-4 text-blue-600" />,
  task: <Calendar className="h-4 w-4 text-purple-600" />,
  deal: <Zap className="h-4 w-4 text-yellow-600" />,
  lead: <Clock className="h-4 w-4 text-cyan-600" />,
}

export function NotificationsList({ compact = false }: NotificationsListProps) {
  const [notifications, setNotifications] = useState<Notification[]>([])
  const [loading, setLoading] = useState(true)
  const [filter, setFilter] = useState<'all' | 'unread'>('all')

  useEffect(() => {
    const fetchNotifications = async () => {
      try {
        const data = await getNotifications() as Notification[]
        setNotifications(data)
      } catch (error) {
        console.error('[v0] Failed to fetch notifications:', error)
        toast.error('Failed to load notifications')
      } finally {
        setLoading(false)
      }
    }

    fetchNotifications()
  }, [])

  const handleMarkAsRead = async (id: string) => {
    try {
      await markNotificationAsRead(id)
      setNotifications((prev) =>
        prev.map((notif) =>
          notif.id === id ? { ...notif, read: true } : notif
        )
      )
    } catch (error) {
      console.error('[v0] Failed to mark notification as read:', error)
    }
  }

  const handleDismiss = (id: string) => {
    setNotifications((prev) => prev.filter((notif) => notif.id !== id))
  }

  const filteredNotifications =
    filter === 'unread'
      ? notifications.filter((n) => !n.read)
      : notifications

  const displayedNotifications = compact
    ? filteredNotifications.slice(0, 5)
    : filteredNotifications

  const unreadCount = notifications.filter((n) => !n.read).length

  if (loading) {
    return (
      <div className="space-y-3">
        {[...Array(3)].map((_, i) => (
          <div key={i} className="space-y-2 p-3 rounded-lg border border-border/50">
            <Skeleton className="h-4 w-32" />
            <Skeleton className="h-3 w-full" />
          </div>
        ))}
      </div>
    )
  }

  if (displayedNotifications.length === 0) {
    return (
      <div className="rounded-lg border border-border/50 border-dashed p-8 text-center">
        <p className="text-muted-foreground">
          {filter === 'unread' ? 'No unread notifications' : 'No notifications'}
        </p>
      </div>
    )
  }

  return (
    <div className="space-y-2">
      {!compact && (
        <>
          <div className="flex items-center justify-between mb-4">
            <div className="flex gap-2">
              <Button
                variant={filter === 'all' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilter('all')}
              >
                All
              </Button>
              <Button
                variant={filter === 'unread' ? 'default' : 'outline'}
                size="sm"
                onClick={() => setFilter('unread')}
              >
                Unread ({unreadCount})
              </Button>
            </div>
          </div>
          <Separator />
        </>
      )}

      <div className="space-y-2">
        {displayedNotifications.map((notification) => (
          <div
            key={notification.id}
            className={cn(
              'flex items-start gap-3 rounded-lg border p-3 transition-colors',
              notification.read
                ? 'border-border/30 bg-muted/20'
                : 'border-border/50 bg-muted/50'
            )}
          >
            <div className="mt-1 flex-shrink-0">
              {NOTIFICATION_ICONS[notification.type]}
            </div>

            <div className="flex-1 min-w-0">
              <div className="flex items-start justify-between gap-2">
                <div className="flex-1">
                  <p className="font-medium text-sm leading-tight">
                    {notification.title}
                  </p>
                  <p className="text-xs text-muted-foreground mt-1 leading-snug">
                    {notification.description}
                  </p>
                </div>
                {!notification.read && (
                  <Badge variant="default" className="flex-shrink-0 text-xs">
                    New
                  </Badge>
                )}
              </div>

              <div className="flex items-center justify-between gap-2 mt-2">
                <span className="text-xs text-muted-foreground">
                  {new Date(notification.createdAt).toLocaleDateString()}
                </span>
                <div className="flex gap-1">
                  {!notification.read && (
                    <Button
                      variant="ghost"
                      size="sm"
                      className="h-6 px-2 text-xs"
                      onClick={() => handleMarkAsRead(notification.id)}
                    >
                      Mark as read
                    </Button>
                  )}
                  <Button
                    variant="ghost"
                    size="sm"
                    className="h-6 w-6 p-0"
                    onClick={() => handleDismiss(notification.id)}
                  >
                    <X className="h-3 w-3" />
                  </Button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {compact && displayedNotifications.length < filteredNotifications.length && (
        <Button variant="outline" className="w-full bg-transparent">
          View All Notifications
        </Button>
      )}
    </div>
  )
}
